.app {
    display: flex;
    flex-direction: column;
    height: 100vh;
  }
  
  .menu {
    display: flex;
    justify-content: space-around;
    background-color: #931D89;
    padding: 10px;
  }
  
  .menu-item {
    color: white;
    text-decoration: none;
    font-size: 1rem;
    padding: 5px 10px;
  }
  
  .menu-item:hover,
  .menu-item.active {
    background-color: #4C546D;
    border-radius: 5px;
  }
  
  .content {
    flex: 1;
    padding: 20px;
    background-color: #f5f5f5;
  }
  