.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: red;
  padding: 8px;
}

.resultText {
  font-size: 20px;
  text-align: center;
  display: flex;
  align-items: center;
  margin-bottom: 4px;
}

.dezenasContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.dezena {
  margin: 0px;
}

.item {
  font-size: 20px;
  color: #931D89;
  text-align: center;
  display: flex;
  align-items: center;
}

.botao {
  background-color: #931D89;
  border: none;
  color: #fff;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 8px;
  transition: background-color 0.5s ease;
}

.botao:hover {
  background-color: #e01bd0;
}

.input {
  color: red;
  padding: 10px;
  font-size: 16px;
  width: 120px;
  border: 1px solid #ccc;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.input:focus {
  background-color: #931D89;
  outline: none;
  color: #f8f8ff;
}

.input::placeholder {
  color: #ccc;
}
