/* styles.css */
body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #931d89;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #931d89;
    width: 100%;
  }
  
  .card {
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
    width: 90%;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .title {
    color: #931d89;
    font-size: 24px;
    margin-top: 20px;
  }
  
  .select {
    margin-top: 20px;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #931d89;
    width: 100%;
    max-width: 300px;
  }
  
  .button {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #931d89;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .button:hover {
    background-color: #7a166d;
  }
  
  .number-box {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20px;
  }
  
  .number-item {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #931d89;
    color: white;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin: 5px;
    font-size: 18px;
  }
  
  .banner {
    width: 100%;
    position: fixed;
    bottom: 0;
    background-color: #fff;
    color: #931d89;
    text-align: center;
    padding: 10px;
  }
  